import  config from './config.js';
import store from './store/store.js';

const visit  = function(params){
	if(!params) params = new Object;
	params.app_id  = config.appId;
	params.device = config.dev;
	let url = config.apiUrl + 'miniapp/visit'; // 
	uni.request({
	    url: url,
	    data: params,
		method:'POST',
		success(){
			console.log('');
		},
		fail(){
			console.log('');
		},
	});
}

const  post   =  function (url,params){
	if(!params) params = new Object;
	params.device = config.dev;
	params.app_id  = config.appId;
	return  new Promise((resolve,reject) => {
		uni.request({
		    url: config.apiUrl + url, //
		    data: params,
			method:'POST',
		    success: (res) => {
				uni.hideLoading(); 
				if(res.statusCode == 200){
					if(res.data.code == 200){
						if(res.data.code == 100){ //
							store.commit('setMember',null);
							uni.removeStorageSync('member-token');
							reject({
								code:res.data.code,
								msg:res.data.msg
							});	
						}else{
							resolve(res.data.data);	
						}
					}else{
						
						uni.showToast({
							icon:'none',
							title:res.data.msg + '#' +res.data.code
						});
						reject({
							code:res.data.code,
							msg:res.data.msg
						});	
					}
				}else{
					
					uni.showToast({
						icon:'none',
						title:""
					});
					reject({
						code:500,
						msg:''
					});
				}  
		    },
			fail:(res)=>{
				uni.hideLoading(); 
				uni.showToast({
					icon:'none',
					title:""
				});
				reject({
					code:500,
					msg:''
				});
			}
		});
		
	});
}


//
const api  = function(url,params){
	if(!params) params = new Object;
	let token = uni.getStorageSync('member-token');
	params.token = token;
	return this.post(url,params);
}

export default {
	post,
	api,
	visit
}