(function () {
    // demoindex.jsinitQiniu()optionsconfiginit(options) updateConfigWithOptions(options)config
    var config = {
        // bucket ECN, SCN, NCN, NA, ASG 5 
        qiniuRegion: '',
        // bucket 
        qiniuBucketURLPrefix: '',

        // uptokenuptoken
        qiniuUploadToken: '',
    
        qiniuShouldUseQiniuFileName: false
    }

    // init(options) sdk
    //  init(options); 
    //  init(options); 
    function init(options) {
        updateConfigWithOptions(options);
    }

    // 
    function updateConfigWithOptions(options) {
        if (options.region) {
            config.qiniuRegion = options.region;
        } else {
            console.error('qiniu uploader need your bucket region');
        }
        if (options.uptoken) {
            config.qiniuUploadToken = options.uptoken;
        } 
        if (options.domain) {
            config.qiniuBucketURLPrefix = options.domain;
        }
		if(options.shouldUseQiniuFileName){
			config.qiniuShouldUseQiniuFileName = options.shouldUseQiniuFileName
		}
    }

    // 
    function upload(filePath, success, fail, options, progress, cancelTask) {
        if (null == filePath) {
            console.error('qiniu uploader need filePath to upload');
            return;
        }
        if (options) {
            updateConfigWithOptions(options);
        }
        if (config.qiniuUploadToken) {
            doUpload(filePath, success, fail, options, progress, cancelTask);
        }  else {
            console.error('qiniu uploader need one of [uptoken, uptokenURL, uptokenFunc]');
            return;
        }
    }

    // 
    function doUpload(filePath, success, fail, options, progress, cancelTask) {
        if (null == config.qiniuUploadToken && config.qiniuUploadToken.length > 0) {
            console.error('qiniu UploadToken is null, please check the init config or networking');
            return
        }
        var url = uploadURLFromRegionCode(config.qiniuRegion);
        var fileName = filePath.split('//')[1];
        if (options && options.key) {
            fileName = options.key;
        }
        var formData = {
            'token': config.qiniuUploadToken
        };
        if (!config.qiniuShouldUseQiniuFileName) {
            formData['key'] = fileName
        }
        var uploadTask = uni.uploadFile({
            url: url,
            filePath: filePath,
            name: 'file',
            formData: formData,
            success: function (res) {
                var dataString = res.data
                try {
                    var dataObject = JSON.parse(dataString);
                    // fileURL
                    var fileURL = config.qiniuBucketURLPrefix  + dataObject.key;
                    dataObject.fileURL = fileURL;
                    dataObject.imageURL = fileURL;
                    if (success) {
                        success(dataObject);
                    }
                } catch (e) {
                    if (fail) {
                        fail(e);
                    }
                }
            },
            fail: function (error) {
                if (fail) {
                    fail(error);
                }
            }
        })

        // 
        uploadTask.onProgressUpdate((res) => {
            progress && progress(res)
        })

        // 
        cancelTask && cancelTask(() => {
            uploadTask.abort()
        })
    }



    // ECN, SCN, NCN, NA, ASG 5 
    function uploadURLFromRegionCode(code) {
        var uploadURL = null;
        switch (code) {
            case 'ECN': uploadURL = 'https://up.qiniup.com'; break;
            case 'NCN': uploadURL = 'https://up-z1.qiniup.com'; break;
            case 'SCN': uploadURL = 'https://up-z2.qiniup.com'; break;
            case 'NA': uploadURL = 'https://up-na0.qiniup.com'; break;
            case 'ASG': uploadURL = 'https://up-as0.qiniup.com'; break;
            default: console.error('please make the region is with one of [ECN, SCN, NCN, NA, ASG]');
        }
        return uploadURL;
    }

    module.exports = {
        init: init,
        upload: upload,
    }
})();